

.description {
  padding: rem(50px);
  box-shadow: 0 rem(5px) rem(10px) rgba(0,0,0, .1);

  &__items {
    margin: 0 rem(-50px);
    columns: 2 auto;
  }

  & p {
    margin: 0 rem(50px) rem(60px);
    font-size: rem(30px);
    line-height: rem(44px);
    font-style: italic;
  }

  &__title {
    padding-left: rem(55px);
    color: #000;
    font-size: rem(32px);
    font-weight: 900;
    font-style: italic;
    text-transform: uppercase;
    position: relative;
    cursor: pointer;

    &::after {
      content: '';
      display: inline-block;
      width: rem(25px);
      height: rem(8px);
      background: $ColorRed;

      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transform-origin: center;
      transition: transform .6s;
    }
  }
}