
.advantages {
  padding: rem(100px) rem(50px);
  
  &__items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 rem(-50px);
    overflow: hidden;
  }

  &__item {
    width: 33.33%;
    text-align: center;
    padding: 0 rem(100px);

    &.animated {

      & .advantages__title {
        transform: rotateX(0deg)  scaleX(1);
        opacity: 1;
      }
      & .advantages__text {
        opacity: 1;
        transform: translateY(0px);
      }
    }
  }

  &__title {
    color: $ColorRed;
    font-family: $FontBebas;
    text-transform: uppercase;
    font-size: rem(60px);
    line-height: rem(60px);
    margin: 0 0 rem(40px);

    transition: all .6s;
    transform: rotateX(90deg) scaleX(3);
    opacity: 0;
  }
  &__text {
    margin: 0;
    font-size: rem(32px);
    line-height: rem(44px);
    font-weight: bold;
    font-style: italic;

    opacity: 0;
    transform: translateY(20px);
    transition: all .8s;
    transition-delay: 600ms;
  }
}