
@media screen and (max-width: $x2l) {
  html {
    font-size: 16px;
  }

  .header {
    &__left, &__right {
      width: 50%;
    }

    &__image {
      margin-top: 30%;
    }

    &.animated {

      & .header__props {
        padding: rem(230px) rem(60px) rem(120px) rem(60px);
      }
    }

    &__props {
      margin: rem(7px) 0 rem(100px);

      &-item {
        padding-left: rem(85px);
        margin-bottom: rem(45px);

        & span.text-1 {
          margin-left: rem(6px);
          font-size: rem(24px);
          line-height: rem(24px);
        }
  
        & span.text-2 {
          font-size: rem(30px);
          line-height: rem(30px);
        }

        &::before {
          width: rem(65px);
          height: rem(65px);
        }
      }
    }
    
    &__title {
      padding: rem(26px) rem(36px);
      top: rem(50px);
      left: rem(80px);

      &-text {
        font-size: rem(62px);
        line-height: rem(62px);
        letter-spacing: rem(3px);
      }
    }
  }

  &__guarantee {
    width: rem(240px);
  }

  &__logo {
    width: rem(250px);
    top: rem(45px);

    &-tagline {
      font-size: rem(60px);
      word-spacing: rem(4px);
    }
  } // header End


  .compact {

    &__title {
      font-size: rem(58px);
      line-height: rem(58px);
    }

    &__text {
      font-size: rem(28px);
      line-height: rem(40px);
    }

    &__icon {
      width: rem(140px);
      margin-bottom: rem(50px);
    }
  }//Compact End

  .description {

    &__items {
      margin: 0 rem(-30px);
    }

    & p {
      margin: 0 rem(30px) rem(50px);
      font-size: rem(26px);
      line-height: rem(40px);
      font-style: italic;
    }
  } // description End

  .data-sheet {

    &__title {
      margin: 0 0 rem(65px);
      padding: rem(25px);
      line-height: rem(28px);
    }
    
    &__items {
      margin: 0 rem(-30px);
    }
    &__item {
      padding: 0 rem(30px);
      font-size: rem(25px);
    }
  }// .data-sheet End

  .advantages {

    &__item {
      padding: 0 rem(70px);
    }
    &__title {
      font-size: rem(54px);
      line-height: rem(54px);
      margin: 0 0 rem(40px);
    }
    &__text {
      font-size: rem(30px);
      line-height: rem(42px);
    }
  } // advantages End


  .banner-red {

    &__text {

      &-1, &-3 {
        font-size: rem(130px);
        margin-top: rem(25px);
      }
      &-2 {
        margin: 0 rem(50px);
        font-size: rem(200px);
      }
    }

  }// banner-red End

}