
.data-sheet {
  padding: rem(100px) rem(50px) 0px;
  overflow: hidden;

  &__items {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin: 0 rem(-40px);

    &-left ,&-right {
      width: 50%;

      & .data-sheet__item:last-child {
        padding-bottom: 0;
      }
    }
  }

  &__item {
    width: 100%;
    padding: 0 rem(40px) 0 rem(45px);
    font-size: rem(26px);
    font-style: italic;

    transition: all 1s;
    opacity: 0;
    transform: translateX(500px);

    &.animated {
      opacity: 1;
      transform: translateX(0);
    }
  }

  &__title {
    margin: 0 0 rem(75px);
    padding: rem(30px);
    line-height: rem(26px);
    letter-spacing: 1px;
    font-weight: 900;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-transform: uppercase;
    background: $ColorSheet;
    color: #fff;
    cursor: pointer;
  }

  &__list {
    display: flex;
    flex-direction: column;

    &-item {
      margin-bottom: rem(20px);
      font-weight: bold;
      display: flex;
      flex-direction: row;
      padding: rem(5px) rem(5px) rem(5px) rem(45px);
      position: relative;

      &:hover {
        background: #ececec;
      }

      &:last-child {
        margin-bottom: 0;
      }

      &.image {
        width: 100%;
        padding-top: 100%;
        padding-left: 0px;
        flex-shrink: 0;
        position: relative;

        &::after {
          visibility: hidden;
          opacity: 0;
        }
        &:hover {
          background: none;
        }

        & img {
          width: 100%;
          height: 100%;
          object-fit: contain;
          position: absolute;
          top: 0;
          left: 0;
        }
      }

      &::after {
        content: '';
        width: 10px;
        height: 10px;
        background: $ColorRed;
        border-radius: 50%;

        position: absolute;
        top: rem(14px);
        left: rem(10px);
      }
    }
    &-text1 {
      width: 60%;
      color: $ColorText2;
      padding-right: rem(5px);
    }
    &-text2 {
      text-align: center;
      width: 40%;
      font-weight: 900;
    }
  }
}