.compact {
  padding: rem(100px) rem(50px);
  display: flex;

  &__image, &__content {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }

  &__img {
    height: 100%;
    width: 100%;
    object-fit: contain;
  }

  &__content {
    &-block {
      max-width: rem(500px);
      text-align: center;
    }
  }
  &__title {
    margin: 0 0 rem(20px);
    white-space: nowrap;

    font-size: rem(60px);
    line-height: rem(60px);
    color: $ColorRed;
    font-family: $FontBebas;
    text-transform: uppercase;
  }
  &__text {
    margin: 0;
    font-size: rem(30px);
    line-height: rem(42px);
    font-style: italic;
    font-weight: bold;
  }
  &__icon {
    width: rem(150px);
    height: auto;
    margin-bottom: rem(60px);
  }
}