
.header {
  width: 100vw;
  min-height: 100vh;
  max-width:100%;
  background: url(../img/imagen-fondo-banner-princial.jpg),
              linear-gradient(to top, rgba(255,255,255, .5), rgba(255,255,255, .5));
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-blend-mode: overlay;

  padding: 0 rem(50px);
  overflow: hidden;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  &__left, &__right {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    position: relative;
  }
  &__left {
    width: 45%;
    flex-shrink: 0;
  }
  &__right {
    align-items: center;
    width: 55%;
  }

  &__image {
    max-width: 1000px;
    max-height: 1000px;
    margin-top: auto;

    &-img {
      width: 100%;
      height: 100%;
      object-fit: contain;

      transform: translateY(100%) scale(.8);
      opacity: 0;
      animation: showimg .6s ease 1 forwards;
      animation-delay: 600ms;

      @keyframes showimg {
        0% {
          transform: translateY(100%) scale(.6);
          opacity: 0;
        }
        100% {
          transform: translateY(0%) scale(1);
          opacity: 1;
        }
      }

    }
  }
  
  &.animated {

    & .header__props {
      padding: rem(280px) rem(80px) rem(140px) rem(80px);

      &-list {
        max-height: 1000px;
        opacity: 1;
      }
      &-item {
        max-height: 300px;
      }
    }

    & .header__guarantee {
      transition: all .8s;
      transition-delay: .4s;
      opacity: 1;
    }

  }

  &__props {
    background: rgba(#ebebeb, .8);
    padding: rem(90px) rem(80px) rem(140px) rem(80px);
    position: relative;
    margin: rem(7px) 0 rem(140px);

    &-list {
      max-height: 0px;
      overflow: hidden;
      opacity: 0;
      transition: all 1s;
    }

    &-item {
      max-height: 0;
      transition: all .5s;
      display: flex;
      flex-direction: column;
      padding-left: rem(105px);
      margin-bottom: rem(75px);
      position: relative;
      // border: 1px solid blue;
      &:last-child {
        margin-bottom: 0;
      }

      & span.text-1 {
        margin-left: rem(7px);
        font-size: rem(28px);
        line-height: rem(28px);
        font-style: italic;
        font-weight: bold;
        color: #1c1c1c;
      }

      & span.text-2 {
        color: #000;
        font-size: rem(35px);
        line-height: rem(35px);
        font-style: italic;
        font-weight: 900;
      }

      &::before {
        content: '';
        background-image: url(../img/sprite-icons.png);
        background-repeat: no-repeat;
        background-position: -1.5% 50%;
        background-size: cover;
        display: inline-block;
        width: rem(80px);
        height: rem(80px);
        // border: 1px solid red;

        position: absolute;
        transform: translateY(-50%);
        top: 50%;
        left: 0;
      }

      &.header-power::before {
        background-position: 16.8% 50%;
      }
      &.header-size::before {
        background-position: 37.5% 50%;
      }
      &.header-compact::before {
        background-position: 59.3% 50%;
        transform: translateY(-58%);
      }
      &.header-programs::before {
        background-position: 81.7% 50%;
      }
      &.header-extras::before {
        background-position: 102% 50%;
      }
    }
  }

  &__title {
    padding: rem(40px) rem(56px);
    position: absolute;
    top: rem(80px);
    left: rem(100px);

    &-text {
      font-family: $FontBebas;
      white-space: nowrap;
      font-size: rem(74px);
      line-height: rem(74px);
      letter-spacing: rem(5px);
      text-transform: uppercase;
      margin: 0;
      color: #fff;
      position: relative;
      z-index: 1;
    }

    &::after {
      content: '';
      width: 100%;
      height: 100%;
      background: $ColorRed;
      border-radius: rem(25px) rem(25px) rem(25px) 0;
      transform: skew(-12deg);

      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
    }
  }

  &__guarantee {
    opacity: 0;
    width: rem(280px);
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 52%);
    & img {
      width: 100%;
    }
  }

  &__logo {
    width: rem(280px);
    text-align: center;
    position: absolute;
    top: rem(85px);
    right: 0;
    &-img {
      width: 100%;
      height: auto;
      position: relative;
      z-index: 1;
    }
    &-tagline {
      font-family: $FontPristine;
      font-size: rem(68px);
      word-spacing: rem(5px);
      position: relative;
      z-index: 1;
    }
    &::after {
      content: '';
      width: 100%;
      height: 100%;
      display: block;
      background: rgba(255,255,255, .6);
      filter: blur(rem(20px));

      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
    }
  }
}