.grid-images {
  padding: rem(50px) rem(50px) 0;
  background-image: linear-gradient(to left, #f2f2f2, #f0f0f0);
  // overflow: hidden;

  &__items {
    // outline: 1px solid red;
    display: flex;
    flex-wrap: wrap;
    margin: 0 rem(-22px);

    &--on .grid-images__img {
      opacity: .4;
      &:hover {
        opacity: 1;
      }
    }
    &--off .grid-images__img {
      transition: opacity .6s;
      opacity: 1;
    }
  }

  &__item {
    width: calc(33.33%);
    padding: 0 rem(22px) rem(130px) rem(22px);
    text-align: center;

    transition: all .8s;
    transform: translateY(300px);
    opacity: 0;
    visibility: hidden;

    &.animated {
      visibility: visible;
      transform: translateY(0px);
      opacity: 1;

      & .grid-images__title {
        opacity: 1;
      }
    }
  }

  &__img {
    width: 100%;
    margin-bottom: rem(25px);
    transition: opacity .3s;
  }

  &__title {
    font-size: rem(30px);
    line-height: rem(50px);
    font-weight: bold;
    font-style: italic;

    opacity: 0;
    transition: all .4s;
    transition-delay: 800ms;
    
  }
}