html {
  font-size: $mainFontSize;
  font-family: $FontPanton;
  min-width: 100%;
  min-height: 100%;
}

body {
  height: 100%;
  width: 100%;
}
* {
  box-sizing: border-box;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.section {
  padding: 0 rem(50px);
}

.title-h2 {
  padding-left: rem(55px);
  margin: 0px 0px rem(65px);
  color: #000;
  font-size: rem(32px);
  font-weight: 900;
  font-style: italic;
  text-transform: uppercase;
  position: relative;

  &::after {
    content: '';
    display: inline-block;
    width: rem(25px);
    height: rem(8px);
    background: $ColorRed;

    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    transform-origin: center;
    transition: transform .6s;
  }
}

.accordion {
  &-btn {
    position: relative;

    &::after, &::before {
      content: '';
      display: inline-block;
      width: rem(20px);
      height: rem(6px);
      background: #555555;
      mix-blend-mode: unset;

      position: absolute;
      right: rem(15px);
      top: 50%;
      transform-origin: center;
      transition: all .3s;
    }

    &::before {
      transform: translateY(-50%) rotate(270deg);
    }
    &::after {
      transform: translateY(-50%) rotate(180deg);
    }

    &:hover::after, &:hover::before {
      background: #fff;
    }

    &.minus {
      &::after , &::before {
        transform: translateY(-50%) rotate(0deg);
      }
    }
  }

  &-item {
    height: 0;
    transition: height .6s;
    overflow: hidden;
  }

  &-content {
    transition: all .8s;
  }
}