.banner-red {
  overflow: hidden;
  width: 100%;
  background-image: url(../img/foto-segon-banner.jpg);
  background-repeat: no-repeat;
  background-size: cover;

  &__content {
    padding: rem(200px) rem(50px);
    background: rgba(167, 24, 21, .8);
    
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__text {
    display: flex;
    flex-direction: row;
    align-items: center;
    mix-blend-mode: overlay;

    transition: all 800ms;
    transform: scale(.5);
    opacity: .5;

    &-1, &-3 {
      font-size: rem(170px);
      font-family: $FontPristine;
      margin-top: rem(20px);
      white-space: nowrap;

      transition: all 1200ms;
      transition-delay: 200ms;
      
      opacity: 0;
    }
    &-1 {
      transform: scale(.4) translateX(200px);
    }
    &-3 {
      transform: scale(.4) translateX(-200px);
    }
    &-2 {
      margin: 0 rem(50px);
      font-size: rem(240px);
      font-family: $FontBebas;
    }

    &.animated {

      transform: scale(1);
      opacity: 1;

      & .banner-red__text-1,
      & .banner-red__text-3 {
        transform: scale(1) translateX(0%);
        opacity: 1;
      }
    }
  }

  &__img {
    display: inline-block;
    width: 100%;
    height: 100%;
    object-fit: contain;
    margin: 0;
  }
}