
@media screen and (max-width: $xl) {
  html {
    font-size: 15px;
  }

  .header {
    background: url(../img/imagen-fondo-banner-princial_1000.jpg),
              linear-gradient(to top, rgba(255,255,255, .5), rgba(255,255,255, .5));
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    &__left {
      width: 45%;
    }
    &__right {
      width: 55%;
      align-items: flex-start;
    }

    &__image {
      margin-top: 40%;
      margin-left: 10%;
    }

    &.animated {

      & .header__props {
        padding: rem(200px) rem(50px) rem(120px) rem(50px);
      }
    }

    &__props {
      margin: rem(7px) 0 rem(100px);
      flex-shrink: 0;

      &-item {
        padding-left: rem(85px);

        & span.text-1 {
          margin-left: rem(6px);
          font-size: rem(24px);
          line-height: rem(24px);
        }
  
        & span.text-2 {
          font-size: rem(30px);
          line-height: rem(30px);
        }

        &::before {
          width: rem(65px);
          height: rem(65px);
        }
      }
    }
    
    &__title {
      padding: rem(24px) rem(36px);
      top: rem(50px);
      left: rem(70px);

      &-text {
        font-size: rem(56px);
        line-height: rem(56px);
        letter-spacing: rem(3px);
      }
    }

    &__guarantee {
      width: rem(220px);
    }

    &__logo {
      width: rem(230px);
      top: rem(55px);
      &-tagline {
        font-size: rem(58px);
        word-spacing: rem(3px);
      }
    }

  } // header End



  .compact {

    &__title {
      font-size: rem(56px);
      line-height: rem(56px);
    }
    
    &__icon {
      width: rem(130px);
      margin-bottom: rem(40px);
    }
  }// compact End

  .advantages {

    &__item {
      padding: 0 rem(50px);
    }

    &__title {
      font-size: rem(52px);
      line-height: rem(52px);
      margin: 0 0 rem(40px);
    }
    &__text {
      font-size: rem(28px);
      line-height: rem(40px);
    }
  } // advantages End

}