
@media screen and (max-width: $xs) {
  html {
    font-size: 11px;
  }

  .compact {

    &__content {
      height: 100%;
      background: rgba(255,255,255, .8);
      position: absolute;
      top: 0;
      left: 0;
      transition: opacity .6s;
    }
  }// compact End

  .grid-images {
    &__item {
      width: 100%;
      padding: 0 rem(22px) rem(100px) rem(22px);
    }

    &__title {
      font-size: rem(32px);
      line-height: rem(50px);
    }
  }// grid-images End

  .data-sheet {

    &__item {
      font-size: rem(27px);
    }
  }// .data-sheet End


  .advantages {
    padding: rem(100px) rem(25px) 0;

    &__items {
      flex-wrap: wrap;
      justify-content: center;
    }
    &__item {
      width: 80%;
    }
  } // advantages End


  .banner-red {

    &__text {

      &-1, &-3 {
        font-size: rem(60px);
        margin-top: rem(25px);
      }
      &-2 {
        margin: 0 rem(20px);
        font-size: rem(100px);
      }
    }

  }// banner-red End

}