
@media screen and (max-width: $lg) {
  html {
    font-size: 14px;
  }

  .header {
    position: relative;

    &__right {
      width: 100%;
      position: relative;
    }
    &__left {
      width: 100%;
    }

    &__left {
      position: absolute;
      left: rem(50px);
      right: rem(50px);
      z-index: 1;
    }

    &__image {
      margin-top: 30%;
      margin-left: 10%;
    }

    &__props {
      // padding: rem(200px) rem(50px) rem(120px) rem(50px);
      margin: rem(7px) 0 rem(100px);
      flex-shrink: 0;
    }

    &__logo {
      width: rem(200px);
      top: rem(55px);
      &-tagline {
        font-size: rem(52px);
        word-spacing: rem(3px);
      }
    }
  }

  .compact {

    &__title {
      font-size: rem(54px);
      line-height: rem(54px);
    }
  }//compact End

  .grid-images {

    &__item {
      padding: 0 rem(22px) rem(100px) rem(22px);
    }

    &__title {
      font-size: rem(26px);
      line-height: rem(44px);
    }
  }// grid-images End

  .data-sheet {

    &__items {
  
      &-left ,&-right {
        width: 100%;
      }
    }

    &__title {
      margin: 0 0 rem(75px);
      padding: rem(26px);
      line-height: rem(28px);
    }

  }// .data-sheet End


  .advantages {

    &__item {
      padding: 0 rem(40px);
    }

    &__title {
      font-size: rem(48px);
      line-height: rem(48px);
      margin: 0 0 rem(36px);
    }
    &__text {
      font-size: rem(26px);
      line-height: rem(38px);
    }
  } // advantages End


  .banner-red {

    &__text {

      &-1, &-3 {
        font-size: rem(100px);
        margin-top: rem(25px);
      }
      &-2 {
        margin: 0 rem(30px);
        font-size: rem(160px);
      }
    }

  }// banner-red End
  
}