
@media screen and (max-width: $md) {
  html {
    font-size: 13px;
  }

  .header {
    background: url(../img/imagen-fondo-banner-princial_700.jpg),
              linear-gradient(to top, rgba(255,255,255, .7), rgba(255,255,255, .7));
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;

    &__right {
      width: 100%;
      position: relative;
      align-items: center;
    }
    &__image {
      margin-top: 20%;
      margin-left: 10%;
    }
    &__logo {
      display: none;
    }

  }


  .compact {

    &__title {
      font-size: rem(52px);
      line-height: rem(52px);
    }

    &__text {
      font-size: rem(26px);
      line-height: rem(38px);
    }
    
    &__icon {
      width: rem(120px);
      margin-bottom: rem(30px);
    }
  }

  .grid-images {

    &__item {
      width: 50%;
      padding: 0 rem(22px) rem(120px) rem(22px);
    }

    &__title {
      font-size: rem(28px);
      line-height: rem(46px);
    }
  }// grid-images End


  .description {

    &__items {
      margin: 0 rem(-25px);
      columns: 1 auto;
    }

    & p {
      margin: 0 rem(30px) rem(50px);
      font-size: rem(28px);
      line-height: rem(42px);
      font-style: italic;
    }
  } // sdescription End


  .advantages {

    &__item {
      padding: 0 rem(25px);
    }
  } // advantages End


  .banner-red {
    background-image: url(../img/foto-segon-banner_800.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    &__text {

      &-1, &-3 {
        font-size: rem(80px);
        margin-top: rem(25px);
      }
      &-2 {
        margin: 0 rem(30px);
        font-size: rem(130px);
      }
    }

  }// banner-red End

}