
@media screen and (max-width: $sm) {
  html {
    font-size: 12px;
  }

  .header {
    padding: 0 rem(25px);

    &__left {
      left: rem(25px);
      right: rem(100px);
    }
    &__right {
      align-items: flex-start;
    }

    &__image {
      margin-top: 60%;
    }

    &.animated {
      & .header__props {
        padding: rem(200px) rem(30px) rem(120px) rem(30px);
      }
    }

    &__props {
      margin: rem(7px) 0 rem(90px);
    }

    &__title {
      padding: rem(24px) rem(36px);
      top: rem(50px);
      left: rem(50px);

      &-text {
        font-size: rem(44px);
        line-height: rem(44px);
        letter-spacing: rem(2px);
      }
    }
  }


  .compact {
    padding: rem(100px) rem(25px);
    position: relative;
    &__image, &__content {
      width: 100%;
      display: flex;
    }

    &__image {
      position: relative;
      z-index: 0;
    }

    &__content {
      height: 100%;
      background: rgba(255,255,255, .8);
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      transition: opacity .6s;

      &-block {
        max-height: 400px;
      }

      &:active {
        opacity: 0;

        & .compact__content-block {
          transition: all .2s;
          transform: scale(.4);
          opacity: .0;
        }
      }
    }

    &__text {
      font-size: rem(28px);
      line-height: rem(40px);
    }
  } // compact End


  .grid-images {
    padding: rem(50px) rem(25px) 0;
    &__items {
      justify-content: center;
    }
    &__item {
      width: 80%;
      padding: 0 rem(22px) rem(120px) rem(22px);
    }

    &__title {
      font-size: rem(34px);
      line-height: rem(52px);
    }
  }// grid-images End

  .description {
    padding: rem(50px) rem(25px) 0;
  } // sdescription End


  .data-sheet {
    
    padding: rem(100px) rem(25px) 0;

    &__item {
      font-size: rem(28px);
    }
  }// .data-sheet End


  .advantages {
    padding: rem(100px) rem(25px) 0;
    overflow: hidden;

    &__items {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    &__item {
      width: 50%;
      padding: 0 rem(25px);
      margin-bottom: rem(70px);
    }

    &__title {
      font-size: rem(52px);
      line-height: rem(52px);
      margin: 0 0 rem(30px);
    }
    &__text {
      font-size: rem(30px);
      line-height: rem(42px);
    }
  } // advantages End


  .banner-red {
    padding: auto rem(25px);

    &__text {

      &-1, &-3 {
        font-size: rem(80px);
        margin-top: rem(25px);
      }
      &-2 {
        margin: 0 rem(20px);
        font-size: rem(120px);
      }
    }

  }// banner-red End

}