
@media screen and (max-width: $x3l) {
  html {
    font-size: 17px;
  }

  .header {

    &__image {
      max-width: 780px;
      max-height: 780px;
    }

    &.animated {

      & .header__props {
        padding: rem(250px) rem(70px) rem(120px) rem(70px);
      }
    }

    &__props {
      margin: rem(7px) 0 rem(100px);

      &-item {
        padding-left: rem(105px);
        margin-bottom: rem(50px);

        & span.text-1 {
          margin-left: rem(6px);
          font-size: rem(26px);
          line-height: rem(26px);
        }
  
        & span.text-2 {
          font-size: rem(32px);
          line-height: rem(32px);
        }

        &::before {
          width: rem(70px);
          height: rem(70px);
        }
      }
    }
    &__title {
      padding: rem(28px) rem(44px);
      top: rem(60px);
      left: rem(90px);
      
      &-text {
        font-size: rem(68px);
        line-height: rem(68px);
        letter-spacing: rem(4px);
      }
    }

    &__guarantee {
      width: rem(260px);
    }

    &__logo {
      width: rem(270px);
      top: rem(65px);

      &-tagline {
        font-size: rem(64px);
      }
    }

  } //header End


  .description {

    &__items {
      margin: 0 rem(-40px);
    }

    & p {
      margin: 0 rem(40px) rem(50px);
      font-size: rem(28px);
      line-height: rem(42px);
      font-style: italic;
    }
  }//description End


  .data-sheet {
    
    &__items {
      margin: 0 rem(-35px);
    }
    &__item {
      padding: 0 rem(35px);
      font-size: rem(26px);
    }
  }// .data-sheet End


  .banner-red {

    &__text {

      &-1, &-3 {
        font-size: rem(150px);
        margin-top: rem(20px);
      }
      &-2 {
        margin: 0 rem(50px);
        font-size: rem(220px);
      }
    }

  }// banner-red ENd

  
}